body {
  margin: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  color: rgb(6, 55, 92);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.background {
  background-image: url("./assets/images/background.jpeg");
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-repeat: no-repeat repeat;
  background-size: cover;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* all areas */

.flex-section {
  display: flex;
}

.font-style {
  color: black;
  text-shadow: 0 0 5px #a5d2ff, 0 0 10px #87c8d4,
  0 0 20px #74b7c3, 0 0 30px #98a2a3,
  0 0 40px #3b9ae8;
}

h1, h2 {
  font-size: 48px;
  font-weight: bold;
  color: white;
  text-shadow: 0 0 5px #a5d2ff, 0 0 10px #87c8d4,
  0 0 20px #74b7c3, 0 0 30px #98a2a3,
  0 0 40px #3b9ae8;
}
button {
  border: solid white 2px;
  padding: 5px;
  border-radius: 10px;
  color: rgb(12, 46, 93);
  box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);

  
}
button:hover {
  background-color: white;
  color: black;
}

a:hover {
  color: black;

}

li:hover {
  color: #010507;
  cursor: pointer;
}

.active {
  color: black;
  border-radius: 5px;
  background: linear-gradient( white, #a9dfea);
  padding: 2px 5px;
}

.navActive {
  color: #010507;
}

/* every page container */
.info-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding: 10px;
  margin: 30px;
  border: solid 1px #19d6e3a3;
  border-radius: 5px;
  height: 90vh;
  background: #52c1e5a3;
  box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);


}


/* nav section */

.nav-section {
  width: 90%;
  height: 90vh;
  border: solid 1px #19d6e3a3;
  border-radius: 5px;
  background: #52c1e5a3;
  box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
  text-align: center;
  display: flex;
  flex-direction: column;
  padding: 3px 0;
  margin: 30px;

  /* flex-column py-3 m-3 text-center  */
}
nav {
  padding: 5px 0 50px 0;
}

nav li {
  padding: 10px;
}

.self {
  margin: auto;
  border-radius: 8px;
  opacity: 98%;
  box-shadow: 0 30px 100px rgba(32, 212, 212, .1)

  
}
.self::after {
  content: "";
  background-color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  opacity: 0.3;
  top: 0;


}

/* projects */
.projects {
  /* margin: 5px 15px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  /* padding: 25px; */
  
}

.projects img {
  border-radius: 8px 8px 0 0;
}

.projects h4 {
  font-size: 18px;
  text-align: center;
  font-weight: bold;
  padding: 5px;
}
.projects p {
  padding: 5px;
}
.single-project {
  border: solid 2px white;
  border-radius: 8px;
  box-shadow: 0 30px 40px rgba(0, 0, 0, 2);
  margin-bottom: 30px;
}

/* resume */
.skills {
  text-align: center;
  width: 100%;
}

.follow {
  /* border: solid 1px #23757a; */
  border-radius: 8px;
  display: flex;
  justify-content: flex-end;
  padding: 15px 10vw;
  background: linear-gradient( white, #a9dfea);
  box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
  margin: 30px 0px;
}

.follow a{
  padding: 0 25px;
}


/* contact section */
input, textarea {
  border-radius: 8px;
}
.address {
  padding: 10px 5px;
  color: white;
  text-shadow: 0 0 5px #2a85df, 0 0 10px #5aaebd,
  0 0 20px #74b7c3, 0 0 30px #bbdcdf,
  0 0 40px #3b9ae8;
}
.icon {
  border-radius: 75px;
  height: 40px;
  padding-top: 5px;
}

.facebook {
  border-radius: 75px;
  height: 40px;
}




@media (max-width: 750px) {
  .info-container {
    /* display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around; */
    width: 100%;
    padding: 10px;
    margin: 30px;
    border: solid 1px #19d6e3a3;
    border-radius: 5px;
    background: #52c1e5a3;
    box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
  
  
  }
  .about {
    font-size: 14px;
  }

  .nav-section {
    width: 50%;
    border: solid 1px #19d6e3a3;
    border-radius: 5px;
    background: #52c1e5a3;
    box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
    text-align: center;
    padding: 3px 0;
    margin: 50px 60px;
  
  }


}

@media (max-width: 600px) {
  .info-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    padding: 10px;
    margin: 30px;
    border: solid 1px #19d6e3a3;
    border-radius: 5px;
    background: #52c1e5a3;
    box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
  
  
  }
  .flex-section {
    display: flex;
    flex-direction: column;
  }
  .about {
    font-size: 14px;
  }

  .nav-section {
    width: 50%;
    border: solid 1px #19d6e3a3;
    border-radius: 5px;
    background: #52c1e5a3;
    box-shadow: 0 30px 40px rgba(139, 190, 242, 0.5);
    text-align: center;
    padding: 3px 0;
    margin: 50px 60px;
  
  }


}




